.lt-label {
  font-family: 'lt-Roboto', Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 36px;
  position: fixed;
  z-index: 1;
  left: 12px;
  top: auto;
  bottom: 38px;
  width: auto;
  max-width: 500px;
  height: 36px;
  margin: 0;
  padding: 0;
  cursor: pointer;
  transform: translate3d(0, 0, 0) rotate(90deg);
  text-indent: 0;
  transition: opacity ease-out 290ms;
  color: #fff;
  border: 0 none;
  border-radius: 0 0 25px 25px;
  background-image: linear-gradient(rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.15) 100%);
  background-clip: padding-box;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.15);
  -webkit-font-smoothing: subpixel-antialiased !important;
}
@media (min-width: 1024px) {
  .lt-label {
    background-color: #ffd000;
    top: auto;
    left: auto;
    right: 0;
    bottom: 12%;
    transform-origin: right top;
  }
}
