@media (min-width: 1024px) {
  .lt-label::after {
    display: block;
  }
}
.lt-label-block {
  display: block;
  transform: rotate(180deg);
  position: relative;
  z-index: 10;
  height: 36px;
  max-width: 310px;
  font-weight: 500;
}
@media (min-width: 1024px) {
  .lt-label-block {
    padding-left: 20px;
    padding-right: 30px;
  }
}
.lt-label-block__txt {
  display: none;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0;
  margin-left: 0;
  padding: 0 10px;
  margin-right: 10px;
  color: #222222;
  white-space: nowrap;
  opacity: 1;
  height: 36px;
  line-height: 36px;
  float: left;
  max-width: 250px;
  z-index: 9998;
}
@media (min-width: 1024px) {
  .lt-label-block__txt {
    display: block;
  }
}
.lt-icon {
  height: 60px;
  width: 60px;
  display: block;
  text-align: center;
  line-height: 36px;
  background: #ffd000;
  border: 2px solid #ffd000;
  color: #ffd000;
  transform: rotate(90deg);
  border-radius: 100%;
  box-shadow: 0 0.12em 0.31em 0 rgba(0, 0, 0, 0.26);
}
@media (min-width: 1024px) {
  .lt-icon {
    position: absolute;
    height: 36px;
    width: 36px;
    top: -11px;
    right: 0;
    left: auto;
    background: #222222;
    box-shadow: none;
  }
}
.lt-icon__img {
  width: 36px;
  height: 36px;
  top: 14px;
  position: relative;
  fill: #222222;
}
@media (min-width: 1024px) {
  .lt-icon__img {
    width: 20px;
    height: 20px;
    top: 5px;
    fill: #ffd000;
  }
}
